  function renderPickupShops(shopListJSON, currentTab) {
    var shopList = JSON.parse(shopListJSON)['servicePoints'];
    var deliveryPanelPickupShops = currentTab.querySelector('.delivery-panel-pickup-shops');
    var shopsHtml = '';
    deliveryPanelPickupShops.classList.add('active');
    shopList.forEach(function (shop) {
      shopsHtml += "<label class=\"shop-container\">\n                      \n                      <div class=\"delivery-panel-pickup-shop\">\n                        <label for=\"".concat('shopID_' + shop.id, "\">\n                          ").concat(shop.name, " (").concat(parseFloat((shop.distance / 1000).toFixed(1)), " km), ").concat(shop.address.street, ", ").concat(shop.address.postalCode || '', " ").concat(shop.address.city, "\n                        </label>\n                      </div>\n                      <button class=\"myBtn  myBtnPrimary pickup-detail-button\" value=\"").concat(shop.id, "\" name=\"shopID\">V\xE6lg</button>                      \n                    </label>\n                    ");
    });
    deliveryPanelPickupShops.innerHTML = shopsHtml;
    var pickupDetailsButtons = [];
    pickupDetailsButtons = currentTab.getElementsByClassName('pickup-detail-button');

    if (pickupDetailsButtons.length != 0) {
      var _loop = function _loop(i) {
        pickupDetailsButtons[i].addEventListener('click', function (event) {
          event.stopPropagation();
          removeErrorClass(deliveryPanelPickupShops);
          hideSearchForm(event);
          localStorage.setItem('deliveryID', currentTab.querySelector('[type="radio"]').value);
          localStorage.setItem('deliveryShop', JSON.stringify(shopList[i]));
          addShopIdToRadioButton(shopList[i].id, currentTab);
          renderShopOpeningHours(shopList[i], currentTab);
        });
      };

      for (var i = 0; i < pickupDetailsButtons.length; i++) {
        _loop(i);
      }

      ;
    }
  }

  function removeErrorClass(deliveryPanelPickupShops) {
    deliveryPanelPickupShops.classList.remove('error');
  }

  function clearDeliveryPanel(panel) {
    while (panel.hasChildNodes()) {
      panel.removeChild(panel.lastChild);
    }
  }

  function hideSearchForm(event) {
    var searchForm = event.target.closest('.delivery-panel-pickup').querySelector('.delivery-panel-pickup-search');
    searchForm.classList.add('hidden');
  }

  function showSearchForm(event) {
    var searchForm = event.target.closest('.delivery-panel-pickup').querySelector('.delivery-panel-pickup-search');
    searchForm.classList.remove('hidden');
  }

  function renderShopOpeningHours(shop, currentTab) {
    var timetableHtml = "\n                          <h4>".concat(shop.name, "</h4>\n                          <p>").concat(shop.address.street, ", ").concat(shop.address.postalCode || '', " ").concat(shop.address.city, "</p>\n                          <button class=\"myBtn myBtnPrimary close-details-btn\">V\xE6elg et andet afhentningssted</button>\n                          <table class=\"timetable\">\n                            <tr>\n                              <th>Days</th>\n                              <th>Time</th>\n                            </tr>");
    shop.openingHours.forEach(function (dayOfWeek) {
      timetableHtml += "<tr>\n                          <td>".concat(dayOfWeek.day, "</td>\n                          <td>").concat(dayOfWeek.hours, "</td>\n                        </tr>");
    });
    timetableHtml += "</table>";
    var shopOpeningHours = currentTab.querySelector('.shop-opening-hours');
    var panelPickupShops = currentTab.querySelector('.delivery-panel-pickup-shops');
    panelPickupShops.classList.remove('active');
    shopOpeningHours.innerHTML = timetableHtml;
    shopOpeningHours.classList.add('visible');
    var closeBtn = shopOpeningHours.querySelector('.close-details-btn');
    closeBtn.addEventListener('click', function (event) {
      hideShopOpeningHours(shopOpeningHours, panelPickupShops, currentTab);
      showSearchForm(event);

      if (currentTab.querySelectorAll('.shop-container').length === 0) {
        requestPickupShops(event);
      }

      removeShopIdFromRadioButton(currentTab);
      removeShopIdFromLocalStorage();
    });
  }

  function addShopIdToRadioButton(shopId, currentTab) {
    currentTab.querySelector('input[type="radio"]').setAttribute('data-shop-id', shopId);
  }

  function removeShopIdFromRadioButton(currentTab) {
    currentTab.querySelector('input[type="radio"]').removeAttribute('data-shop-id');
  }

  function hideShopOpeningHours(shopOpeningHours, panelPickupShops) {
    shopOpeningHours.classList.remove('visible');
    panelPickupShops.classList.add('active');
  }
